<template>
	<view @touchstart="chumo_kaishi" @touchend="chumo_jieshu" style="width: 100vw;height: 100vh;">
		
		<view class="box" >
			<view class="cu-bar search bg-white">
				<view class="search-form round">
					<text class="cuIcon-search"></text>
					<input v-model="xinwen_guanjianzi" type="text" placeholder="搜索通知" confirm-type="search"></input>
				</view>
				<view class="action">
					<button @click="xinwen_sousuo()" class="cu-btn bg-blue shadow-blur round">搜索</button>
				</view>
			</view>
		</view>
		
		<!-- 轮播图 -->
<!-- 		<swiper class="screen-swiper" style="height: 28vh;" :class="dotStyle?'square-dot':'round-dot'" :indicator-dots="true"
		 :circular="true" :autoplay="true" interval="5000" duration="500">
			<swiper-item  v-for="(item,index) in swiperList" :key="index">
				<image  :src="item.url" mode="aspectFill"></image>
			</swiper-item>
		</swiper> -->
		<!-- 轮播图 -->
		
		
		<view style="background: #ffffff;margin-bottom: 12rpx;margin-top: 12rpx;" class="hengfang">
			<view @click="xuanze_liebiao('')" style="width: 25vw;padding-top: 2vw;padding-bottom: 2vw;">
				<view style="width: inherit;margin-left: 5vw;"><image style="width: 15vw;height: 15vw;" src="/static/png/suoyou.png"></image></view>
				<view style="width: inherit;text-align: center;margin-top: 10rpx;">所有信息</view>
				<view v-if="noticeType == ''" class="dingwei_juedui" style="width: 25vw;height: 1vw;background: #56bb91;margin-top: 1vw;"></view>
				<view v-if="noticeType != ''" class="dingwei_juedui" style="width: 25vw;height: 1vw;background: #F1F1F1;margin-top: 1vw;"></view>
			</view>
			<view  @click="xuanze_liebiao(1)" style="width: 25vw;padding-top: 2vw;padding-bottom: 2vw;">
				<view style="width: inherit;margin-left: 5vw;"><image style="width: 15vw;height: 15vw;" src="/static/png/gonggao.png"></image></view>
				<view style="width: inherit;text-align: center;margin-top: 10rpx;">社区公告</view>
				<view v-if="noticeType == 1" class="dingwei_juedui" style="width: 25vw;height: 1vw;background: #56bb91;margin-top: 1vw;"></view>
				<view v-if="noticeType != 1" class="dingwei_juedui" style="width: 25vw;height: 1vw;background: #F1F1F1;margin-top: 1vw;"></view>
			</view>
			<view  @click="xuanze_liebiao(2)" style="width: 25vw;padding-top: 2vw;padding-bottom: 2vw;">
				<view style="width: inherit;margin-left: 5vw;"><image style="width: 15vw;height: 15vw;" src="/static/png/xinwen.png"></image></view>
				<view style="width: inherit;text-align: center;margin-top: 10rpx;">社区新闻</view>
				<view v-if="noticeType == 2" class="dingwei_juedui" style="width: 25vw;height: 1vw;background: #56bb91;margin-top: 1vw;"></view>
				<view v-if="noticeType != 2" class="dingwei_juedui" style="width: 25vw;height: 1vw;background: #F1F1F1;margin-top: 1vw;"></view>
			</view>
			<view  @click="xuanze_liebiao(3)" style="width: 25vw;padding-top: 2vw;padding-bottom: 2vw;">
				<view style="width: inherit;margin-left: 5vw;"><image style="width: 15vw;height: 15vw;" src="/static/png/luntan.png"></image></view>
				<view style="width: inherit;text-align: center;margin-top: 10rpx;">社区其它</view>
				<view v-if="noticeType == 3" class="dingwei_juedui" style="width: 25vw;height: 1vw;background: #56bb91;margin-top: 1vw;"></view>
				<view v-if="noticeType != 3" class="dingwei_juedui" style="width: 25vw;height: 1vw;background: #F1F1F1;margin-top: 1vw;"></view>
			</view>
		</view>
		
<!-- 		<view class="cu-card article " style="margin:1rpx" v-for="item in list" :key="item.value">
			<view  @click="tiaozhuan_tuwen()" class="cu-item shadow">
				<view class="title" >
					<view class="text-cut">{{ item.shangpin_mingcheng }}</view>
				</view>
				<view class="content" >
					<view class="desc">
						<view class="text-content">{{ item.shangpin_miaoshu }}</view>
						<view>
							<view class="cu-tag bg-red light sm round">发布人：{{ item.yonghu_nicheng }}</view>
							<view class="cu-tag bg-green light sm round">{{ item.shangpin_riqi }}</view>
						</view>
					</view>
					<image :src="item.shangpin_tupian_1" mode="aspectFill"></image>
				</view>
			</view>
		</view> -->
		
		<view @click="tiaozhuan_tuwen([item.noticeId])" style="width: 100vw;" v-for="(item,index) in list" :key="index">
			<view style="font-size: 120%;margin-left: 3vw;font-weight: 600;">{{ item.noticeTitle }}</view>
			<view class="hengfang" style="margin-top: 1vw;margin-bottom: 1vw;color: #c2c2c2;">
				<view style="width: 79vw;margin-left: 3vw;">{{ item.createBy }}<text style="margin-left: 2vw;margin-right: 2vw;">|</text>{{ item.createTime }}</view>
				<view style="width: 18vw;"><text class="cuIcon-footprint">10086</text></view>
				</view>
			<image style="width: 96vw;height: 40vw;margin-left: 2vw;" class="huxing_quan_mini" mode="aspectFill" :src="item.bannerUrl"></image>
		<view style="width: 96vw;font-size: 100%;margin-top: 1vw;margin-left: 2vw;color:#c2c2c2 ;display: -webkit-box;-webkit-box-orient: vertical;-webkit-line-clamp: 3;overflow: hidden;">
				{{ item.noticeContent}}
			</view> 
			<view style="width: 100vw;height: 12rpx;background: #F1F1F1;margin-top: 1vw;"></view>
		</view>

		<view v-if="false" class="cu-card article duan"  v-for="(item,index) in list" :key="index">
			<view  @click="tiaozhuan_tuwen([item.noticeId])" class="cu-item shadow" style="margin: 15rpx;">
				<view class="title">
					<view class="text-cut">{{ item.noticeTitle }}</view> 
				</view>
				<view class="content">
					<view class="desc">
						<view class="text-content">{{ item.noticeContent}}</view>
						<view>
							<view class="cu-tag bg-red light sm round">发布人：{{ item.createBy }}</view>
							<view class="cu-tag bg-green light sm round">{{ item.createTime }}</view>
						</view>
					</view>
					<image :src="item.bannerUrl" mode="aspectFill"></image>
				</view>
			</view>
		</view>
		
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				swiperList: [{
						id: 0,
						type: 'image',
						url: getApp().globalData.public_server_img + 'img/lb_1.png'
					},
					{
						id: 0,
						type: 'image',
						url: getApp().globalData.public_server_img + 'img/lb_2.png'
					},
				],
				dotStyle: true,
				list: [],
				start_x:0,
				start_y:0,
				end_x:0,
				end_y:0,
				noticeType:"",
				xinwen_guanjianzi:"",
				
			}
		},
		methods: {
			tiaozhuan_tuwen: function(res) {
				uni.navigateTo({
					url: "../modular/shequ_gonggao/shequ_gonggao?id=" + res[0]
				})
			}  ,
			chumo_kaishi:function(e){
				return
				// console.log('kaishi')
				this.start_x = e.changedTouches[0].clientX
				this.start_y = e.changedTouches[0].clientY
			},
			chumo_jieshu:function(e){
				// console.log('jieshu')
				return
				this.end_x = e.changedTouches[0].clientX
				this.end_y = e.changedTouches[0].clientY
				
				let _this = this
				let kaishi_x = _this.start_x
				let kaishi_y = _this.start_y
				let jieshu_x = _this.end_x
				let jieshu_y = _this.end_y
				
				let cha_x = jieshu_x - kaishi_x
				let cha_y = jieshu_y - kaishi_x
				
				if(cha_x>150){
					
					// uni.showModal({
					// 	title:"左滑"
					// })
					uni.reLaunch({
						url:"../index/index"
					})
					
				}else if(cha_x<-150){
					
					// uni.showModal({
					// 	title:"右滑"
					// })
					
					uni.reLaunch({
						url:"../index_4/index_4"
					})
				}
				
			}
			,
			get_news_all:function(){
				let _this =  this
				_this.list = []
				let data = {
					quartersId:getApp().globalData.public_user_property_id,
					noticeType:_this.noticeType,
				}
				_this.wangluo_qingqiu('/wx/ipms/getnoticelist', data, true, true, 'GET').then(value => {
					if (value) {
						_this.list = value
					}
				})
			},
			xuanze_liebiao:function(res){
				this.noticeType = res
				this.get_news_all()
			},
			xinwen_sousuo:function(){
				let _this =  this
				
				if(!_this.xinwen_guanjianzi){
						uni.showModal({
							title: '提示',
							content: "新闻关键字不能为空",
							showCancel: false,
							success: function() {}
						});
						return false;
				}
				
				
				_this.list = []
				let data = {
					quartersId:getApp().globalData.public_user_property_id,
					noticeTitle:_this.xinwen_guanjianzi,
				}
				_this.wangluo_qingqiu('/wx/ipms/getnoticelist', data, true, true, 'GET').then(value => {
					if (value) {
						_this.list = value
						_this.xinwen_guanjianzi = ""
					}
				})
			}
			
		},
		onLoad:function(){
			this.get_news_all()
		},
		onPullDownRefresh: function() {
			let _this = this;
			_this.get_news_all()
			uni.stopPullDownRefresh();
		}
	}
</script>

<style>
.duan{
	margin-top: -16rpx;
}
</style>
